/* Base Styles for Navigation Menu */
.navigation-menu {
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 5px;
    padding: 6px;
    font-family: "Montserrat", sans-serif;
  }
  
  .navigation-menu li {
    margin: 0 15px;
  }
  
  .navigation-menu li a {
    text-decoration: none;
    color: white;
    font-size: 18px;
    transition: color 0.3s ease-in-out;
  }
  
  .active {
    color: #FCECA1 !important;
    font-weight: bold !important;
  }
  
  /* Mobile Navigation Styles */
  .mobile-navigation {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .mobile-navigation li {
    margin-top: 20px;
  }
  
  .mobile-navigation li a {
    text-decoration: none;
    font-size: 1.4rem;
    color: black;
  }
  
  /* Book Now Button Styling */
  .book-now {
    background-color: transparent;
    color: white;
    /* margin-top: 10px; */
    font-size: 16px;
    font-family : '"Montserrat", sans-serif';
    padding: 12px 20px;
    border: 2px solid white;
    border-radius: 25px;
    cursor: pointer;
    font-weight: bold;
    transition: all 0.3s ease;
  }
  
  .book-now:hover {
    background-color: #FCECA1;
  
    border-color: #FCECA1;
  }

  .Navbar_MenuIcon {
    display: none !important;
  }


  /* RESPONSIVE STYLES */
  
  /* Mobile View */
  @media (max-width: 800px) {
    .navigation-menu {
      display: none; /* Hide desktop menu on mobile */
      font-family: "Montserrat", sans-serif;
     ;
    }
    .Navbar_MenuIcon {
      display: flex !important;
    
    }
  
    .mobile-navigation li a {
      font-size: 1.4rem; /* Larger font size for mobile */
      font-family : '"Montserrat", sans-serif';
    }
  
    .book-now {
      width: 70%;
      border-color:#296248 ;
      font-family : '"Montserrat", sans-serif';
      color:black;
      font-size: 18px;
      padding: 10px 20px;
      margin-top:10px !important;
    }
    .book-now:hover {
      background-color: #FCECA1;
      font-family : '"Montserrat", sans-serif';
      border-color: #FCECA1;
    }
    
  }
  
  /* Tablet and Desktop View */
  @media (min-width: 800px) {
    .navigation-menu {
      font-family : '"Montserrat", sans-serif';
      display: flex;
    }

   
  
  }
  
  /* Large Screen Desktop View (Optional) */
  @media (min-width: 1024px) {
    .navigation-menu li {
       font-family: "Montserrat", sans-serif;
      margin: 0 30px; /* Increased margin on larger screens */
    }
  
    .book-now {
      font-size: 18px; /* Larger button on large screens */
      padding: 14px 28px;
      font-family : '"Montserrat", sans-serif';
    }
  }
  